import {callbackErrorHandler} from '../lib/util.js';
import { includeReview } from '../lib/util.js';

import appSettings from '../settings.js';

var browser;

if (!browser) {
    browser = chrome;
}

var urlParams = new URLSearchParams(window.location.search);
var originalUrl = urlParams.get('url');

var pageName = false;

var setPageName = function() {
    if (pageName) {
        document.getElementById('page-name').innerHTML = pageName;
        document.title = 'Currently Pausing '+pageName;
    } else {
        document.title = 'Currently Pausing '+window.location.hostname;
    }
};

var setReviewFooter = function(isShowReviewFooter) {
    if (!isShowReviewFooter) {
        document.getElementById('footer').classList.add('hidden');
    }
};

// ask the backend pause page related data
browser.runtime.sendMessage({
    'subject': 'getPausePageData',
    'url': originalUrl,
}, callbackErrorHandler( function(result){
    pageName = result.blockedUrlName;
    setPageName();

    setReviewFooter(result.isShowReviewFooter);
}));

browser.runtime.onMessage.addListener(callbackErrorHandler(function (msg, sender, response) {
    // get html content, not used at the moment
    if ((msg.subject === 'extensionInactive')) {
        window.location.replace(originalUrl);
    }
}));

window.onload = function(){
    includeReview();
    setPageName();

    document.getElementById('footer-hide-button').addEventListener('click', function (event) {
        browser.runtime.sendMessage({
            'subject': 'setIsShowReviewFooter',
            'isShowReviewFooter': false,
        });
        setReviewFooter(false);

    });
};
