import Mustache from 'mustache';

const urlParser = require('url');

export const SECONDS_IN_HOUR = 3600;
export const SECONDS_IN_MINUTE = 60;
export const MINUTES_IN_HOUR = 60;
export const HOURS_IN_DAY = 24;
export const MILLIS_IN_SECOND = 1000;

export function callbackErrorHandler(originalCallback) {
    // wrapper function for wrapping chrome API callbacks
    // workaround for chrome not sending errors to onerror listener for callbacks
    return function(...parameters){
        try {
            return originalCallback(...parameters);
        } catch (e) {
            console.error(e);
            throw e;
        }
    };
}

export function includeReview() {
    const reviewTemplateId = process.env.MICROSOFT ? 'review-no-href-tmpl' : 'review-tmpl';
    const reviewTemplate = document.getElementById(reviewTemplateId).innerHTML;
    Mustache.parse(reviewTemplate);

    document.getElementById('review-container').innerHTML = Mustache.render(reviewTemplate);
}

export function getHours(seconds) {
    return Math.floor(seconds / SECONDS_IN_HOUR);
}

export function getMinutes(seconds) {
    return Math.ceil((seconds % SECONDS_IN_HOUR) / SECONDS_IN_MINUTE);
}

export function convertToMinutes(seconds) {
    return getHours(seconds) * MINUTES_IN_HOUR + getMinutes(seconds);
}

export function convertToDays(seconds) {
    return Math.floor(seconds / HOURS_IN_DAY / SECONDS_IN_HOUR);
}

export function getSecondsDiff(time1, time2) {
    return Math.round(Math.abs(time1 - time2) / MILLIS_IN_SECOND);
}
